@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@800&display=swap);
@font-face {
  font-family: 'RalewayLight';
  src: local('RalewayLight'), url(/Recipe-App/static/media/Raleway-Light.f25e9b3a.ttf) format('truetype');
}

body {
  margin: 0;
  overflow-x: hidden;
  font-family: RalewayLight, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



@font-face {
    font-family:  'RalewayBold'; 
    src: local('RalewayBold'), url(/Recipe-App/static/media/Raleway-Bold.d1a96e2a.ttf) format('truetype');

  }
  
.secondSectionWrapper{
    width: 100vw;
    height: 900px ;
    background: #2a2c39; 
    display: -webkit-flex; 
    display: flex;   
    -webkit-justify-content: center;   
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;    
    position: relative;

}

.hideHelper{
    display: none;
}

.formWrapper{
    position: absolute;
    top: 70vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.recipesWrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    position: relative;
    width: 800px;
    height: 400px;
    overflow: hidden;
    background: rgb(211, 211, 211);
}

.recipeCounter{
    position: absolute;
    top: 20%;
    border: 1px solid #a1a1a1;
    border-radius: 100px;
    padding: 10px 15px;
    color: white;
}

.changeButtonWrapper{
    position: absolute;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 800px;
    top: 20%;
    margin-top: 15px;
}
.changeButton{   
    background: transparent;
    width: 40px;
    height: 40px;
    border: 0;
    fill: #a1a1a1;
    cursor: pointer;
}
.changeButton:hover{
    fill: white;
}
.changeButton.previous{
    -webkit-transform: rotate(180deg) translateY(3px);
            transform: rotate(180deg) translateY(3px);
}

.recipeWrapper{
    position: relative;
    display: -webkit-flex;
    display: flex;
    width: 500px;
    margin-right: 500px;
    background: rgb(128, 1, 1);
}


.infoAboutRecipe{
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 500px;
    height: 100%;
    position: absolute;
    top: 0;
    left: 100%;
    background: #d1d1d1;
}

.title{
    margin: -15px 0px 15px;
    font-family: RalewayBold;
    width: 90%;
    line-height: 40px;
    font-size: 40px;
}

.mealType{
    width: 300px;
    margin: 20px;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.type{
    padding: 10px;
}
.mealType::before{
    content: '';
    width: 100%;
    height: 1.5px;
    position: absolute;
    top: -10px;
    right: 0;
    background: sienna;
}
.mealType::after{
    content: '';
    width: 100%;
    height: 1.5px;
    position: absolute;
    bottom: -10px;
    right: 0;
    background: sienna;
}

.recipeDescription{
    padding: 10px 0px 0px;
    width: 60%;
    color: #2a2c39;
}

.tdWrapper{
    width: 300px;
    margin: 10px;
    display: -webkit-flex;
    display: flex;  
    -webkit-justify-content: space-around;  
            justify-content: space-around;
}

.recipeInfo{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.showIngredientsBtn{
    background: sienna;
    padding: 7px 20px;
    border: 2px solid sienna;
    border-radius: 200px;
    color:white;
    cursor: pointer;
    transition-duration: .1s;
}

.showIngredientsBtn:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.calories{
    position: absolute;
    bottom: 5px;
    right: 20px;
    font-weight: bold;
}

