.image{
    width: 300px;
    height: 400px;
    object-fit: cover;
}

.searchWrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.searchWrapper input{
    width: 180px;
    height: 20px;
    transition-duration: .1s;
}
.searchWrapper input:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);    
}
.searchWrapper input:focus{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);    
}

button[type='submit']{
    width: 35px;
    height: 35px;
    background: url(/Recipe-App/static/media/magnifier.6630d2ca.svg) center no-repeat;
    margin-left: 5px;
    border: 0;
    cursor: pointer;
    transition-duration: .1s;
}
button[type='submit']:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
button[type='submit']:active{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.sortBtn{
    margin: 10px 0px;
    border: 1px solid #a1a1a1;
    padding: 5px;
    background: transparent;
    color: #a1a1a1;
    cursor: pointer;
    transition-duration: .1s;
}
.sortBtn:hover{
    border: 2px solid #a1a1a1;
}
.setSort{
    color: white;
}

input{

    border-radius: 200px;
    padding: 5px 8px;
    border: 1px solid black;
    background: transparent;
}

input:focus{
    outline: none;
    border: 2px solid black;
}
