.header{
    width: 100vw;
    height: 9vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    position: absolute;
    background: rgba(24, 24, 24, 0.42);
}

.mainLogo{
    fill: white;
    -webkit-transform: scale(2) translateX(-5px);
            transform: scale(2) translateX(-5px);
